.main-header {
  position: relative;
  background-color: @color-main-light;

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
    padding: 24px 30px 0;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    width: 1220px;
    padding: 0 0;
    margin: 53px auto 0;
  }
}

.main-header__wrapper {
  display: flex;
  padding: 13px 20px 15px;
  align-items: center;

  @media (min-width: @tablet-width) {
    width: 174px;
    height: 52px;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    width: 202px;
  }
}
